
.auth__login-container{
    margin-top: 5%;
    
}
.auth__login-form-1{
    
    border-radius: 5px;
    background-color: lightgray;
    padding: 1%;
    width: 50%;
}
.auth__login-form-1 h3{
    text-align: center;
    color: #333;
}
.auth__login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 1%;
 
    
}
.auth__login-form-2 h3{
    text-align: center;
    color: #fff;
}
.auth__login-container form{
    padding: 10%;
}
.auth__btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
    &:focus{outline: none;}
    
}
.auth__login-form-1 .auth__btnSubmit{
    color: #fff;
    background-color: #0062cc;
    &:hover{background-color: darken(#0062cc, 10);}
}
.auth__login-form-2 .auth__btnSubmit{
    background-color: #fff;
    color: #0062cc;

}
.auth__login-form-2 .auth__ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}
.auth__login-form-1 .auth__ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}

.auth__social-networks{
    
    
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 10px;
    width: 100%;


}
