.calendar__calendar-screen{
    display: flex;
    flex-flow: column;
    height: 90vh;
    margin: 10px
}

.calendar__fab{
    border-radius: 100%;
    bottom: 45px;
    font-size: 25px;
    padding: 15px;
    position: fixed;
    right: 45px;
}

.calendar__fab-delete{
    border-radius: 100%;
    bottom: 45px;
    font-size: 25px;
    padding: 15px;
    position: fixed;
    right: 110px;
}

